
import React, { Component } from 'react';
import { Redirect } from 'react-router';

class Leftside extends Component {
   
state={
    redirect:''
}

dropdowmenu(ev){
    let dv=document.getElementById("item1");
    

    if(dv.classList.contains("open")){
        dv.classList.remove("open");

    }else{
        dv.classList.add("open");

    }

}


redict=(Data)=>{

this.setState({redirect:Data});

}
    
    render() {
        if (this.state.redirect) {
            return <Redirect push to={this.state.redirect}/>;
          }       
          
          return (
            <div class="left-sidebar-pro">
            
            <nav id="sidebar">
                <div class="sidebar-header">
                    <a href="#"><img src="/home/zackprod/eclipse-workspace/Music_Product/E_Commerce/WebContent/img/logo.png" width="100%" height="100%" alt="" />
                    </a>
                    <h3>ZackProd</h3>
                    <p>Developer</p>
                    <strong>AP+</strong>
                </div>
                <div class="left-custom-menu-adp-wrap">
                    <ul class="nav navbar-nav left-sidebar-menu-pro">
                        <li class="nav-item">
                            <a  href="/" data-toggle="dropdown" role="button" aria-expanded="false" class="nav-link dropdown-toggle"><i class="fa big-icon fa-home"></i> <span class="mini-dn">Home</span> <span class="indicator-right-menu mini-dn"><i class="fa indicator-mn fa-angle-left"></i></span></a>
                            
                        </li>
                     
                        <li  class="nav-item " id="item1" onClick={this.dropdowmenu} ><a data-toggle="dropdown" role="button" aria-expanded="true" class="nav-link dropdown-toggle"><i class="fa big-icon fa-edit"></i> <span class="mini-dn">UPDATE</span> <span class="indicator-right-menu mini-dn"><i class="fa indicator-mn fa-angle-left"></i></span></a>
                            <div role="menu"  class="dropdown-menu left-menu-dropdown form-left-menu-std animated flipInX">
                                <a   href="/update"  class="dropdown-item" onClick={()=>this.redict("/Add")}>Update Operator </a>
                                <a class="dropdown-item" onClick={()=>this.redict("/Update")} >Add Operator</a>
                                <a  class="dropdown-item" onClick={()=>this.redict("/Remove")}> Remove Operator</a>
                                <a class="dropdown-item">Multi Upload</a>
                                <a class="dropdown-item">Text Editor</a>
                                <a class="dropdown-item">Dual List Box</a>
                            </div>
                        </li>

                       
                    </ul>
                </div>
            </nav>
        </div>
        );
      }


}
export default Leftside;

