import React, { Component } from 'react';
import ReactResizeDetector from 'react-resize-detector';
import axios from 'axios'

import Graph from './graph';
import { Redirect } from 'react-router-dom'





class Dashboard extends Component {

  constructor(props) {
    super(props)
    this.state = {
      idoperateur: 0,
      alloperateur: ''
    }
  }


  async componentWillMount() {

    let response = await axios.get('http://localhost:8765/operateurreeltime');
    let jsonData = await JSON.parse(JSON.stringify(response.data));
   if(jsonData!=null){
    let test = jsonData;
    var idoperateurr = test[0].idoperateur;

    this.setState({ idoperateur: idoperateurr});
    this.setState({alloperateur: jsonData});
   }
  }


  change = (data) => {

    this.setState({ idoperateur: data })
    this.state.idoperateur = data;

  }


  render() {
    try {
      if (this.state.alloperateur.length > 0) {
        let test = this.state.alloperateur;
        var shift = test[0].shift;
            
        var html;
        console.log("operateur===>",this.state.idoperateur);

        html = test.map(function (item, i) {
          if (test[i].idoperateur == this.state.idoperateur) {
            return (<li class='actif' onClick={() => { this.change(test[i].idoperateur) }}  ><span class="message-serial message-cl-one ">{test[i].idoperateur}</span> <span class="message-info">{test[i].shift} | {test[i].cat_piece}</span> <span class="message-time">{test[i].get_tic_time}</span></li>);
          } else {
            return (<li onClick={() => { this.change(test[i].idoperateur) }}  ><span class="message-serial message-cl-one ">{test[i].idoperateur}</span> <span class="message-info">{test[i].shift} | {test[i].cat_piece}</span> <span class="message-time">{test[i].get_tic_time}</span></li>);

          }
        }, this);


        return (
          <div class="welcome-adminpro-area">








            <div class="container-fluid">
              <div class="row">
                <div class="col-lg-2 col-md-2 col-sm-12 col-xs-12"  >
                  <div class="welcome-wrapper shadow-reset res-mg-t mg-b-30" >
                    <div class="welcome-adminpro-title">
                      <h1>Delphi</h1>
                      <p>Shift : {shift} :</p>
                    </div>
                    <div class="adminpro-message-list">

                      <ul class="message-list-menu">

                        {html}


                      </ul>

                    </div>
                  </div>
                </div>




                <Graph id={this.state.idoperateur} />

              </div>

            </div>
          </div>



        );


      }else{
        console.log(this.state.alloperateur);
        return(<div></div>);
      }
    } catch (error) {
     
    }


  }








}
export default Dashboard